
import http from 'js/http'
import Loading from 'vux/src/components/loading/index.vue'

import {mapState, mapMutations, mapGetters} from 'vuex'

export default {
name: "index",
components: {
Loading,
},
data() {
return {
currencyList: [],
showLoading: false,
listShow:true
}
},
methods: {
openCollect() {
this.$router.push({name: 'collect', params: {address: this.walletData.address}})
},
getCurrencyList() {
http.post('/wallet/currency', {
}, (res) => {
this.listShow = false
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
this.currencyList = res.content

sessionStorage.setItem("currencyList", JSON.stringify(this.currencyList));
})
},
pageInt(){
if (!this.loginMsg){
this.$router.push({
path: `/login`
})
return
}
if (!this.currencyList.length){
this.getCurrencyList()
}

}
},
created() {
this.pageInt()
},
activated(){
this.pageInt()
},
computed: {
...mapGetters([
"loginMsg",
]),
},
}



